.pymessage {
    position: fixed;
    top: 5%;
    width: 600px;
    max-width: 100%;
    padding: 20px;
    font-size: 20px;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: green;
    color: white;
    box-shadow: 0px 2px 5px 4px rgba(255, 255, 255, 0.5);
}

/* remove */

.avatars-container {
    position: fixed;
    width: 600px;
    max-width: 100%;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    justify-content: space-around;
    box-shadow: 0px 2px 5px 4px rgba(255, 255, 255, 0.5);
    align-content: center;
    gap: 20px;
    border-radius: 15px;
    top: 0;

}

.avatars-container .avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: white;
    padding: 4px;
}

.avatars-container img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 50%;
    cursor: pointer;
}