.form-container {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
    align-items: center;
    background-color: #ffffff;

}

.form-container .brand {
    display: flex;
    align-items: center;
    gap: 1rem;
    justify-content: center;

}

.form-container img {
    height: 5rem;
}

.form-container h1 {
    color: rgb(0, 0, 0);
    font-size: 28px;
}

.form-container form {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    background-color: rgba(255, 255, 255, 0.463);
    border-radius: 2rem;
    padding: 3rem 5rem;

}

.form-container form input {
    background-color: transparent;
    padding: 1rem;
    border: 0.1rem solid #4e0eff;
    border-radius: .4rem;
    color: rgb(0, 0, 0);
    font-size: 1.1rem;
    width: 100%;
}

.form-container form input:focus {
    border: .1rem solid #997af0;
    outline: none;
}

.form-container form button {
    background-color: #997af0;
    color: #fff;
    padding: 1rem 2rem;
    border: none;
    font-weight: bold;
    cursor: pointer;
    border-radius: 0.4rem;
    font-size: 1rem;
    text-transform: uppercase;
    transition: .5s ease-in-out;

}

.form-container form button:hover {
    background-color: #4e0eff;
}

.form-container form span {
    color: rgb(0, 0, 0);
    font-size: 20px;
    color: #4e0eff;
}

.form-container form span a {
    color: #4e0eff;
    text-decoration: none;
    font-weight: bold;
}

.form-container .error {
    background-color: red;
    padding: 1rem;
    color: #fff;
    border-radius: .5rem;
    position: fixed;
    bottom: .5%;
    font-size: 1rem;
}

.form-container .message {
    background-color: green;
    color: white;
    border-radius: .5rem;
    padding: 1rem;
}