.dashboard {
    position: fixed;
    background-color: rgb(255, 255, 255);
    width: 600px;
    max-width: 100%;
    height: calc(100%);
    left: 50%;
    padding: 40px;
    transform: translateX(-50%);
    z-index: 250;
}


.passport-container {
    width: 90px;
    height: 90px;
    margin: auto;
    margin: 10px auto;
}

.passport-label {
    width: 100%;
    height: 100%;
    display: block;
    background-size: cover;
    border-radius: 50%;
    box-shadow: 0px 2px 2px 2px rgba(0, 0, 0, 0.3);
}

#passport {
    display: none;
    width: 0;
    height: 0;
    overflow: hidden;
}

.dashboard-form {
    padding: 10px;

}

.dashboard-form .form-group {
    margin-bottom: 15px;
}

.dashboard-form .form-group input {
    padding: 5px 10px;
    border-radius: 8px;
    outline: 0;
    border: 1px solid rgba(0, 0, 0, 0.1);
    font-size: 18px;


}

.success {
    background-color: green;
    padding: 5px 20px;
    font-size: 18px;
    color: white;
    border-radius: 8px;

}

.wrapper {
    height: 60vh;
    overflow: auto;
    padding-bottom: 50px;
    background-color: rgb(255, 255, 255);
    width: 100%;
}

.log-out-wrapper {
    display: flex;
    justify-content: space-between;
}

.log-out-wrapper a {
    text-decoration: none;
    color: white;
}