.avatar-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: #131324;
    gap: 3rem;
    height: 100vh;
    width: 100vw;

}

.avatar-container .loader {
    max-inline-size: 100%;

}

.title-container h1 {
    color: white;

}

.avatar-container .avatars {
    display: flex;
    gap: 2rem;
}

.avatar-container .avatar {
    border: 0.4rem solid transparent;
    padding: 0.4rem;
    border-radius: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .5s ease-in-out;
}

.avatar-container img {
    height: 6rem;
}

.avatar-container .avatar.selected {
    border: 0.4rem solid #4e0eff;

}


.avatar-container button {
    background-color: #997af0;
    color: #fff;
    padding: 1rem 2rem;
    border: none;
    font-weight: bold;
    cursor: pointer;
    border-radius: 0.4rem;
    font-size: 1rem;
    text-transform: uppercase;
    transition: .5s ease-in-out;

}

.avatar-container button:hover {
    background-color: #4e0eff;
}

.avatar-container .error {
    background-color: red;
    padding: 1rem;
    color: #fff;
    border-radius: .5rem;
    font-size: 1rem;
}